import React, { Component } from 'react';
import Modal from 'react-responsive-modal';
import AboutMe1 from './AboutMe1';
import AboutMe2 from './AboutMe2';
import AboutMe3 from './AboutMe3';

export default class Me extends Component
{
  constructor() {
    super();

    this.state = {
      modalIsOpenMe1: false,
      modalIsOpenMe2: false,
      modalIsOpenMe3: false
    };

    this.openModalMe1 = this.openModalMe1.bind(this);
    this.openModalMe2 = this.openModalMe2.bind(this);
    this.openModalMe3 = this.openModalMe3.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  openModalMe1() {
    this.setState({modalIsOpenMe1: true});
  }
  openModalMe2() {
    this.setState({modalIsOpenMe2: true});
  }
  openModalMe3() {
    this.setState({modalIsOpenMe3: true});
  }

  closeModal() {
    this.setState({
      modalIsOpenMe1: false,
      modalIsOpenMe2: false,
      modalIsOpenMe3: false
    });
  }

  render()
  {
    return(
    <div className="col align-self-center">
      {/*<h4 className="cover-heading introduction"><u>About Me</u></h4>*/}

      <div className="about-me-images">
        <img onClick={this.openModalMe1} src="../images/me1.png" alt="About_Me_1" />
        <img onClick={this.openModalMe2} src="../images/me3.png" alt="About_Me_3" />
        <img onClick={this.openModalMe3} src="../images/me2.png" alt="About_Me_2" />

        <Modal open={this.state.modalIsOpenMe1} onClose={this.closeModal}
          little
        >
          <AboutMe1 />
        </Modal>

        <Modal open={this.state.modalIsOpenMe2} onClose={this.closeModal}
          little
        >
          <AboutMe2 />
        </Modal>

        <Modal open={this.state.modalIsOpenMe3} onClose={this.closeModal}
          little
        >
          <AboutMe3 />
        </Modal>
      </div>
    </div>
    )
  }
}



