import React, { Component } from 'react';
import Modal from 'react-responsive-modal';

export default class Stack extends Component
{
  state = {
    open: false,
  };

  onOpenModal = () => {
    this.setState({ open: true });
  };

  onCloseModal = () => {
    this.setState({ open: false });
  };

  render()
  {
    const { open } = this.state;
    return(
    <div className="container">
      <div className="row justify-content-center">
        <div className="col-8 stacks">
          <img  className="stack-logos php-logo" src="../images/php.png" alt="PHP" />
          <img  className="stack-logos mysql-logo" src="../images/mysql.png" alt="MySQL" />
          <img  className="stack-logos react-logo" src="../images/reactjs.png" alt="React.js" />
          <img  className="stack-logos html-css-js-logo" src="../images/html5-css-javascript-logo.png" alt="html5-css3-js" />
        </div>

        <div className="col-md-8 show-full-stack">
          <button type="button" className="btn btn-light" onClick={this.onOpenModal}>even more...</button>

          <Modal open={open} onClose={this.onCloseModal} little>
            <div className="full-stack">
              <h5><u>Full Stack</u></h5>
              <p>
                PHP (version 5/7), Apache, MySQL, HTML5, CSS3, JavaScript, Java, Git, SVN, NPM
              </p>
              <p>
                <b>Frameworks and Libraries: </b>ReactJS, Laravel, Concrete5, Codeigniter, Bootstrap, Kohana
              </p>
              <p>
                <b>I am familiar with: </b>AdonisJS, NodeJS, Express, APIs, OOP, MVC, C#, Adobe Photoshop, Adobe Premiere Pro,
                Sublime Text, Vim, MongoDB, Laravel Valet, Homestead, Vagrant, Unit Test, Firebase, Amazon Web Services,

              </p>
            </div>
          </Modal>
        </div>
      </div>
    </div>
    )
  }
}



